import Trip from '../../components/common/trip.vue'
export default{
	components:{
		Trip,
	},
	data(){
		return{
			info:{},
			id:null,
		}
	},
	onShow(){
		let id=uni.getStorageSync("childId")
		this.id=id?id:""
		this.getTrip()
	},
	methods:{
		getTrip(){
		//孩子id
			this.$http.post("puridingrecord/yestList",{
				childrenId:1
				// childrenId:this.id
			}).then(res=>{
				if(res.code==100){
					this.info=res.info
				}
			})
		},
	}
}
